.contenedor-de-banner{
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    background: #81c784 ;
}
.contenido-banner{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    color:  #e0e0e0 ;
    text-align: center;
}
.link-boton{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.link-boton a{
    margin: 10px;
    
}
.aparecer-back-boton{
    background-color: #4caf50 !important;
}
.no-apareser-boton{
    background-color: #4caf50 !important;
    box-shadow: none !important;
}


/***********objetivo de la pagina*/


.body{
    width: 100%;
    display: flex;
    flex-direction: column;
}


.informacion-page-objetivo{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
}
.letra-objetivo{
    width: 70%;
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    color: #212121;
    font-family: 'Rajdhani', sans-serif;
    font-family: 'Yanone Kaffeesatz', sans-serif;
}


/*targetas*/
.body-targetas{
    margin-top: 50px;
}
.contenedor-targetas{
    width: 100% ;
    display:flex;
    flex-direction: column !important;
}
.targeta-100{
    width: 100%;
    display: flex;
    justify-content: center;
}
.targetas{
    width: 600px;
    display: flex;
}

.img-targeta{
    width: 26%;
    height: auto;
}
.img-targeta img{
    width: 100%;
}

.link-targeta{
    width: 60%;
    height: auto;
    display: flex;
    align-items: center;
    margin-left: 10px;
}
.link-targeta a{
    text-decoration: none;
    font-size: 25px;
    font-family: 'Rajdhani', sans-serif;

    font-family: 'Yanone Kaffeesatz', sans-serif;
    color: #424242;
}
.link-targeta a:hover{
    color: #7FCE08 ;
    text-decoration-line: underline;
}
@media screen and (max-width:1200px) {
    .img-targeta{
        width: 22%;
        height: auto;
    }
}
@media screen and (max-width:500px) {
    .targetas{
        width: 600px;
        display: flex;
    }
    .img-targeta{
        width: 40%;
        height: auto;
    }
}



